
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Search from 'vux/src/components/search/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'

import Scroll from "@/components/scrollAll";
import NoData from '../../components/noData';
export default {
components: {
Flexbox,
FlexboxItem,
XHeader,
Scroll,
Search,
CheckIcon,
NoData
},

data() {
return {
selectId:0,
clientList: [],
pageParams: {
customerId:0,
orderState:4386,
beginDealDate:"",
endDealDate:"",
page: 1,
count: 10,
total: 10
},
pullUp: {
threshold: 0,
txt: {
more: "加载更多",
noMore: "没有更多了"
}
},
pullDown: {
threshold: 50,
stop: 20
},
isSort: false
};
},
methods: {

onChecked(item){
this.$bus.emit("orderSelected", item);
this.$router.back()
},
getData(type) {
let _url = "/Order/GetPagedOrderList";

if(type == 1){
this.pageParams.page = 1;
}
this.Axios.get(_url, {params:this.pageParams}).then(res=>{
let _list = res.data.Data.List;
_list.map(item=>{
if(item.ID == this.selectId){
item.checked = true;
}else{
item.checked = false;
}
})
if (_list.length > 0) {
if (type === 2) {
this.clientList = [
...this.clientList,
..._list
];
this.$refs.scroll.scroll.finishPullUp();
} else {
this.clientList = _list;
this.$refs.scroll.scroll.finishPullDown();
this.$refs.scroll.forceUpdate(true);
}
this.pageParams.page += 1;
this.$refs.scroll.refresh();
} else {
this.$refs.scroll.forceUpdate();
}
});
},
},
mounted() {
this.pageParams.customerId = this.$route.query.cid;
this.selectId = this.$route.query.oid
this.getData();
},
watch: {
clientList() {
setTimeout(() => {
this.$refs.scroll.forceUpdate(true);
}, 500);
}
},

};



